*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


main,section,div,header,nav{
    display: flex;
    flex-wrap: wrap;
}

main{
    width: 100%;
    height: 100vh;
    align-items: center;
    justify-content: center;
    color: white;
    text-transform: capitalize;
    font-family: 'Nunito', sans-serif;
    background-image: url(../img/dominik-schroder-FIKD9t5_5zQ-unsplash.jpg);
    background-size: cover;
}
main>section{
    width: 60%;
    height: 80%;
    background-color: #252525da;
    justify-content: center;
    border-radius: 15px;
    padding: 10px;
}
main>section>header{
    width: 100%;
    justify-content: center;
    height: 25%;
}
main>section>header>nav{
    width: 100%;
    justify-content: center;
    position: relative;
    
}
.myicon{
    position: absolute;
    font-size: 18px;
    right: 18%;
    top: 22px ;


}
main>section>header>nav>input{
    width: 70%;
    background: transparent;
    margin: 10px 0;
    outline: none;
    border:1px solid rgba(255, 255, 255, 0.777);
    color: white;
    height: 50px;
    border-radius: 25px;
    padding: 7px;
    font-size: 18px;
}
#box{
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
#info{
    width: 100%;
    justify-content: space-between;
    align-items: center;
}
#info>div:nth-of-type(1){
    width: 50%;
    flex-direction: column;
    align-items: center;
    height: 100%;
    padding: 3%;
}

#info>div>h3,#info>div>h2,#info>div>h1{
    width: 100%;
}
#weather{
    width: 100%;
    line-height: 2;
}
#myicon{
    width: 40%;
    font-size: 150px;
    align-items: center;
    
    justify-content: center;
}
#loc_info{
    flex-direction: row;
    width: 100%;
    height: 100px;
    justify-content: space-around;
    align-items:center;
}

#loc_info div{
    align-items: center;
}
#loc_info i{
    font-size: 28px;
    margin-right: 5px;
}